/*
 *
 * photo-block-aden
 *
 */
.photo-block-aden {
  position: relative;
  filter: hue-rotate(-20deg) contrast(0.9) saturate(0.85) brightness(1.2);
  --photo-block-image-filter: hue-rotate(-20deg) contrast(.9) saturate(.85) brightness(1.2)blur(var(--photo-block-blur, 0));
}
.photo-block-aden img {
  width: 100%;
  z-index: 1;
}
.photo-block-aden:before {
  content: "";
  display: block;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  position: absolute;
  pointer-events: none;
  z-index: 2;
}
.photo-block-aden:after {
  content: "";
  display: block;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  position: absolute;
  pointer-events: none;
  z-index: 3;
}
.photo-block-aden::after {
  background: linear-gradient(to right, rgba(66, 10, 14, 0.2), transparent);
  mix-blend-mode: darken;
}

